export default {
  data () {
    return {}
  },
  mounted () {
    this.goJump()
  },
  methods: {
    goJump: function (){
      var URLParams = new Array();
      var str ='';
      var params = location.href.match(/[a-zA-Z0-9]*=[a-zA-Z.0-9]*/g);
      for (let i=0; i < params.length ; i++){
          var aParam = params[i].split('=');
          URLParams[aParam[0]] = aParam[1];
        }
      var mw = decodeURIComponent(URLParams["mw"]) + "?";         //节点的"mw"
      delete URLParams['mw'];
      for(let key in URLParams){
        mw += key + "=" + URLParams[key] + '&'
      }
      mw.slice(0,mw.length-1)

      //判断是否微信打开
      var ua = navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == "micromessenger") {
            return true;
        } else {
          console.log(mw)
           this.$router.push({path:'/GameJump'});
           // window.open("https://"+mw)
           location.href="mwsdk://"+mw;
           //location.href="https://www.baidu.com";
            return false;
        }

    }
  }
}



