import axios from 'axios'
import baseUtil from '../../baseUtil'
export default {
  data () {
    return {
      selected: '',
      textValue:'',
      inPValue:'',
      game: 'doudizhu',
      reporter: '5a6efca88fcbe23d70cb460a',
      desc: '',
      contact: '',
      list: [
        {text:'登陆问题', value: 'login'},
        {text:"充值问题", value: 'pay'},
        {text:'游戏问题', value: 'game'},
        {text:'规则问题', value: 'rule'},
        {text:'重大BUG', value: 'bug'},
        {text:'修改建议', value: 'idea'}
      ]
    }
  },
  methods: {
    submit: function () {
      axios({
        method: 'POST',
        url: '/api/v1/feedbacks',
        data: {
          game: 'doudizhu',
          type: this.selected,
          reporter: this.reporter,
          desc: this.textValue,
          contact: this.inPValue,
        }
      }).then((res)=>{
        this.$vux.toast.show({
         text: '感谢提交~'
        })
      })
    }
  }
}



