const redEnvelope = r => require.ensure([], () => r(require('../page/redEnvelope/redEnvelope')), 'redEnvelope')
const shareRed = r => require.ensure([], () => r(require('../page/shareRed/shareRed')), 'shareRed')
const invitingFriends = r => require.ensure([], () => r(require('../page/invitingFriends/invitingFriends')), 'invitingFriends')
