const CuratorFirst = r => require.ensure([], () => r(require('../page/becomeCurator/CuratorFirst')), 'CuratorFirst')
const UserAgreement = r => require.ensure([], () => r(require('../page/becomeCurator/UserAgreement/UserAgreement')), 'UserAgreement')
const bindPhone = r => require.ensure([], () => r(require('../page/becomeCurator/bindPhone/bindPhone')), 'bindPhone')
